<template>
  <div class="dashboard-tabs elevation-1">
    <v-tabs
      v-model="tabModel"
      active-class="tab-item-active"
      :height="65"
      :slider-size="0"
    >
      <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-schedules' }"
      >
        Agendamentos
      </v-tab>
      <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-scheduling-requests' }"
      >
        <v-badge
          light
          :color="tabModel === 1 ? 'success' : 'primary'"
          :content="qtdSchedulingRequests"
          :value="qtdSchedulingRequests"
        >
          Solicitaes
        </v-badge>
      </v-tab>
      <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-revenues' }"
      >
        Faturado
      </v-tab>
    </v-tabs>
    <div class="tabs-period" v-show="false">
      <date-picker-range
        v-if="this.periodModel === 'period'" 
        :outlined="true"
        :dense="true"
        :value="dateRangeValue"
        cssClass="date-rage"
      />
      <select-field
        v-else
        :value="periodModel"
        class="period" 
        :items="periods"
        :outlined="true"
        :dense="true"
        @onChange="changePeriod"
      />
    </div>
  </div>
</template>

<script>
import DatePickerRange from '@/components/globais/form/DatePickerRange'
import SelectField from '@/components/globais/form/select-field/SelectField'
import DateMixin from '@/mixins/DateMixin'
export default {
  name: 'DashboardTabs',
  components: { DatePickerRange, SelectField },
  mixins: [ DateMixin ],
  data: () => ({
    tabModel: 0,
    periodModel: 'today',
    periods: [
      { text: 'Hoje', value: 'today' },
      { text: 'Semana', value: 'week' },
      { text: 'Ms', value: 'month' },
      { text: 'Ano', value: 'year' },
      { text: 'Personalizar', value: 'period' }
    ],
    dateRangeValue: [],
    qtdSchedulingRequests: 0
  }),
  mounted () {
    this.tabModel = this.$route.meta.tab
    const date = new Date()
    this.dateRangeValue = [
      this.formatDateToDateEn(date),
      this.formatDateToDateEn(date)
    ]
  },
  methods: {
    changePeriod (val) {
      this.periodModel = val
      if (val === 'period') {
        window.console.log('if')
      } else this.onChangedPeriod(this.getPeriod(val))
    },
    getPeriod (period) {
      let today = new Date()
      let startDate = this.formatDateToDateEn(today)
      let endDate = null
      const firstDayOfMonth = new Date(today.getFullYear(), today.getMonth(), 1)
      const lastDayOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0)
      const lastDayOfYear = new Date(new Date().getFullYear(), 11, 31)
      switch (period) {        
        case 'week':
          var days = 6 - today.getDay()
          today.setDate(today.getDate() + days)
          endDate = this.formatDateToDateEn(today)
          break;
        
        case 'month':
          startDate = this.formatDateToDateEn(firstDayOfMonth)
          endDate = this.formatDateToDateEn(lastDayOfMonth)
          break;

        case 'year':
          startDate = this.formatDateToDateEn(firstDayOfMonth)
          endDate = this.formatDateToDateEn(lastDayOfYear)
        break;
      
        default:
          endDate = this.formatDateToDateEn(today)
          break;
      }
      return {
        startDate,
        endDate
      }
    },
    onChangedPeriod (period) {
      this.$emit('onChange', period)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/theme.scss';
.dashboard-tabs {
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  align-items: center;
  background: #fff;
}
.tab-item { letter-spacing: 0; font-size: 1rem; padding: 0 40px; }
.tab-item-active { background: #000; color: $success; }
.tabs-period { padding: 0 30px; }
.period { border-radius: 45px; }
</style>

<style lang="scss">
.dashboard-tabs {
  .date-rage { border-radius: 45px !important; width: 215px; }
}
</style>